"use client";

import { useEffect, useState } from "react";
import { usePathname, useRouter } from "next/navigation";
import { Sidebar, MobileSidebarButton } from "@/app/components/Sidebar";

export default function AuthLayout({ children }: { children: React.ReactNode }) {
  const router = useRouter();
  const pathname = usePathname();
  const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null);

  useEffect(() => {
    const auth = localStorage.getItem("auth") === "true";
    setIsAuthenticated(auth);
    const publicPaths = ["/login", "/registration"];

    // Redirect to login if not authenticated
    if (!auth && !publicPaths.includes(pathname)) {
  router.push("/login");
}

    // Redirect to main page if already logged in and on login
    if (auth && publicPaths.includes(pathname)) {
  router.push("/dashboard");
}
  }, [pathname, router]);

  if (isAuthenticated === null) return null; // prevent flicker

  if (!isAuthenticated) return <>{children}</>; // show login page

  // Full-page layout after login
  return (
    <div className="flex min-h-screen w-screen bg-muted/10">
      {/* Desktop sidebar */}
      <div className="hidden md:block md:w-[25%] lg:w-[20%]">
        <Sidebar showLogout />
      </div>

      {/* Main content */}
      <main className="flex-1 p-6 w-full overflow-auto">{children}</main>

      {/* Mobile sidebar button */}
      <MobileSidebarButton />
    </div>
  );
}
